<template>
  <div>
    <h1>后台登陆</h1>
    <van-cell-group>
      <van-field v-model="name" :error="err" placeholder="请输入用户名" />
    </van-cell-group>
    <van-cell-group>
      <van-field
        type="password"
        v-model="password"
        :error="err"
        :error-message="errpwd"
        placeholder="请输入密码"
      />
    </van-cell-group>
    <van-button type="default" v-on:click="check">登陆</van-button>
  </div>
</template>
<style>
.web {
  height: 100%;
  position: relative;
  z-index: -6;
}
</style>
<script>
import Vue from "vue";
import axios from "axios";
import { Button } from "vant";
import { Field } from "vant";
import { Cell, CellGroup } from "vant";
import { Toast } from "vant";

Vue.use(Toast);
Vue.use(Cell).use(CellGroup);
Vue.use(Field);
Vue.use(Button);
export default {
  name: "HelloWorld",
  data() {
    return {
      address: "/",
      // address: "http://localhost:3000/",
      name: "",
      password: "",
      err: false,
      errpwd: ""
    };
  },
  methods: {
    check: function() {
      console.log(1);
      axios
        .post(this.address + "logadmin", {
          name: this.name,
          password: this.password
        })
        .then(response => {
          console.log(response);
          if (response.data !== "no") {
            this.$router.push({
              path: "serve"
            });
          } else {
            this.err = true;
            this.errpwd = "请检查用户名或密码";
            Toast("用户名或密码有误～");
          }
        });
    }
  },
  beforeCreate: function() {
    document.getElementsByTagName("body")[0].className = "web";
  }
};
</script>