    },
    selectable(row) {
      if (row.state == "已通过") {
        return false;
      } else {
        return true;
      }
    },
    selectable1(row) {
      if (row.state == "已结算") {
        return false;
      } else {
        return true;
      }
    },
    choose(a) {
      console.log(a);
      return (this.chooseid = a);
    },
    choose1(a) {
      console.log(a, 1);
      return (this.chooseid1 = a);
    },
    passall() {
      Dialog.confirm({
        title: "提示",
        message: "是否一键通过当前所选内容？"
      })
        .then(() => {
          // on confirm
          axios
            .post(this.address + "passall", { chooseid: this.chooseid })
            .then(res => {
              console.log(res);
              if (res.data !== "no") {
                Toast("操作成功~");
              } else {
                Toast("操作失败，请重新选择~");
              }
            });
        })
        .catch(() => {
          // on cancel
        });
    },
    pass(a) {
      Dialog.confirm({
        title: "提示",
        message: "是否确认通过？"
      })
        .then(() => {
          // on confirm
          axios
            .post(this.address + "pass", {
              _id: a
            })
            .then(res => {
              if (res.data !== "no") {
                Toast("通过成功～");
              }
              if (res.data == "no") {
                Toast("请不要重复通过～");
              }
            });
        })
        .catch(() => {
          // on cancel
        });
    },
    id1(a) {
      this.id = a;
    },
    close(a, b) {
      if (a == "cancel") {
        b(true);
      } else {
        if (this.res !== "") {
          b();
        }
        if (this.res == "") {
          b(false);
        }
      }
    },
    nopass() {
      if (this.res == "") {
        this.errres = true;
      } else {
        axios
          .post(this.address + "res", { _id: this.id, res: this.res })
          .then(res => {
            if (res.data !== "no") {
              Toast("成功～");
