      let date1 = this.value[1];
      axios
        .post(this.address + "searchadmin", {
          name: this.sname,
          card: this.scard,
          number: this.snumber,
          date: date,
          date1: date1,
          ls: this.lsname,
          state: this.state
        })
        .then(res => {
          this.arr = res.data;
        });
    },
    // 添加银行
    add() {
      axios
        .post(this.address + "add", {
          name: this.sms,
          ifid: this.ifid,
          ifname: this.ifname,
          ifcard: this.ifcard,
          renumber: this.renumber
        })
        .then(res => {
          console.log(res);
          if (res.data == "no") {
            Toast("已存在该银行，请勿重复添加!");
          } else {
            Toast("添加成功～");
          }
        });
    },
    handleCommand2(a) {
      console.log(a);
      if (a == 1) {
        this.ifid = "是";
      } else {
        this.ifid = "否";
      }
    },
    handleCommand3(a) {
      if (a == 1) {
        this.ifname = "否";
      } else {
        this.ifname = "是";
      }
    },
    handleCommand4(a) {
      if (a == 1) {
        this.ifcard = "否";
      } else {
        this.ifcard = "是";
      }
    },
    handleCommand5(a) {
      if (a == 1) {
        this.renumber = "否";
      } else {
        this.renumber = "是";
      }
    },
    //禁用银行
    ban(a) {
      console.log(a);
      axios.post(this.address + "ban", { _id: a }).then(res => {
        console.log(res);
        if (res.data == "ok") {
          Toast("成功");
        }
      });
    },
    //添加团队
    findteacher: function() {
      let that = this;
      axios.post(this.address + "findteacher", {}).then(res => {
        console.log(res);
        that.teacher = res.data.length;
        axios
          .post(this.address + "addinfo", {
            name: that.name,
            password: that.password,
            level: that.radio,
            teacher: that.teacher + 1,
            class: "00"
          })
          .then(response => {
            console.log(response);
            if (response.data === "no") {
              Toast("该用户已经存在");
            }
            if (response.data === "ok") {
              Toast("开通成功");
            }
          });
      });
    },
    submit: function() {
      let that = this;
      if (this.radio == "1") {
        axios
          .post(this.address + "addinfo", {
            name: that.name,
            password: that.password,
            level: that.radio,
            teacher: "00",
            class: "00"
          })
          .then(response => {
            console.log(response);
            if (response.data === "no") {
              Toast("该用户已经存在");
            }
            if (response.data === "ok") {
              Toast("开通成功");
            }
          });
      }
      if (this.radio == "2") {
        this.findteacher();
      }
      if (this.radio == "3") {
        if (this.value1 == 0) {
          Toast("请选择老师");
        }
        if (this.value1 !== 0) {
          axios
            .post(this.address + "addinfo", {
              name: that.name,
              password: that.password,
              level: that.radio,
              teacher: "00",
              class: that.value1
            })
            .then(response => {
              console.log(response);
              if (response.data === "no") {
                Toast("该用户已经存在");
              }
              if (response.data === "ok") {
                Toast("开通成功");
              }
            });
        }
      }
    },
    //查看详情
    toteam(a) {
      console.log(a);
      axios.post(this.address + "toteam", { teacher: a.teacher }).then(res => {
        console.log(res.data);
        this.$router.push({
          path: "submit",
          query: {
            level: res.data.level,
            teacher: res.data.teacher,
            class: res.data.class,
            user: res.data.name
          }
        });
      });
    },
    //禁用登陆
    banstudent(a) {
      console.log(a);
      axios.post(this.address + "banstudent", { _id: a._id }).then(res => {
        console.log(res);
        if (res.data == "ok") {
          Toast("成功");
        }
      });
    }
  },

  mounted() {
    let that = this;
    axios.post(this.address + "sall").then(res => {
      this.arr = res.data;
    });
    axios.post(this.address + "sallpass").then(res => {
      this.passarr = res.data;
    });
    axios.post(this.address + "getls").then(res => {
      res.data.forEach(element => {
        that.ls.push(element);
      });
    });
  },
  created() {
    let that = this;
    axios.post(this.address + "findstudent", {}).then(res => {
      this.student = res.data;
    });
    axios.post(this.address + "findteacher", {}).then(res => {
      console.log(res.data);
      this.team = res.data;
      res.data.forEach(element => {
        that.option1[element.teacher] = {
          text: element.name,
          value: element.teacher
        };
      });
    });
  },
  beforeCreate: function() {
    document.getElementsByTagName("body")[0].className = "web";
  }
};
</script>